body {
  margin: 0;
  background-color: #f4f4f4;
  font-family: 'Arial', sans-serif;
  color: #333;
  line-height: 1.6;
}

#navigation a {
  color: #005f73;
  text-decoration: none;
  font-size: 18px;
  margin: 0 10px;
}

#navigation a:hover {
  color: #00afb9;
  text-decoration: underline;
}

input[type="text"],
textarea {
  width: 95%;
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
}

input[type="text"]:focus,
textarea:focus {
  border-color: #008ba3;
  box-shadow: 0 0 5px rgba(0, 139, 163, 0.5);
  outline: none;
}

input[type="button"] {
  padding: 10px 20px;
  font-weight: bold;
  background-color: #008ba3;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s;
}

input[type="button"]:hover {
  background-color: #005f73;
}

hr {
  border: none;
  border-top: 1px solid #bcbcbc;
  margin: 20px 0;
  opacity: 0.8;
}

a {
  color: #008ba3;
  text-decoration: none;
}

a:hover {
  color: #005f73;
  text-decoration: underline;
}

pre {
  font-family: 'Courier New', Courier, monospace;
  padding: 10px;
  background: #f4f4f4;
  border-radius: 4px;
  overflow-x: auto;
}

#latex-editor, #container {
  margin: 0px auto;
  padding: 10px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  height: 100%;

  display: flex;
  justify-content: space-between;
  align-items: normal;
  margin-top: '60px';
}

/* Mobile screen styles */
@media (max-width: 768px) {
  .App {
    max-width: 600px;
  }

  #sidebar {
    display: none;
  }
}

/* Wide screen styles */
@media (min-width: 769px) {
  #latex-editor, #container {
    max-width: 1000px;
  }

  #sidebar {
    display: block;
  }
}

#latex-editor-toolbar {
  display: inline;
  gap: 10px;
  margin-bottom: 10px;
  width: 90%;
}



/* Style for Markdown tables */
table {
  width: 100%;
  border-collapse: collapse;
  margin: 10px 0;
}

table,
th,
td {
  border: 1px solid black;
}

th,
td {
  padding: 8px;
}

th {
  background-color: #f2f2f2;
}